import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchIcons, setSearchTerm, changeSideIcon, setSelectedIcon } from "../../actions/iconActions";
import { bindActionCreators } from "redux";
import { BREAKPOINT_LG,TRANSITION,LIGHT_BACKGROUND,BUTTON_RADIUS,DARK_BORDER_COLOR,MAX_WIDTH,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM} from '../types';
import styled from 'styled-components';
const StyledSearch = styled.div`
    .search-cont {
      
    transition:${TRANSITION};
      &.active{
        background: #fff;
        box-shadow: 0 0px 0px 3px rgba(81, 158, 250, 0.85);
        .search-box {

          input{
          color: ${FONT_COLOR};
        }
        
      }
      }
      &.fixed {
        position: fixed;
        top: 70px;
        left: 0px;
        padding: 5px;
        z-index: 11;
        border-radius: 0;
        right: 0;
        max-width: none;
        background: #fff;
        border: 0px;
        border-top: 1px solid #dee3eb;
        border-bottom: 1px solid #dee3eb;
        .search-box {
          max-width: ${MAX_WIDTH};
          border-bottom: 0px solid #ddd;
        }
      }
     
 
    
      &.active {
        
      }
      .search-box input::-ms-clear {
            display: none;
        }
        @media screen and (max-width: ${BREAKPOINT_LG}) {
            margin: 0px 0px;
            margin-bottom: 0px;
            max-width: none;
            &.fixed {
                margin: 0px;
              }
        }
        .search-box{
            
              position: relative;
              border-radius: ${BUTTON_RADIUS};
              margin: 0 auto;
              display: flex;
            .close {
              order: 1;
              align-self: center;
              padding: 0px 15px;
              height: 32px;
              cursor: pointer;
            }
            input {
              height: 40px;
              margin-left: 60px;
              box-shadow: none;
              outline: none;
              font-weight: 600;
              order: 1;
              color: #73859d;

              font-size: 18px;
              width: 100%;
              margin: 0;
              padding: 0;
              border-radius: 2px;
              border: 0;
              display: block;
              background: transparent;
              margin-left: 10px;
              @media screen and (max-width: ${BREAKPOINT_MD}) {
                height: 40px;
                  font-size: 18px;
              }
            }
            i {
                @media screen and (max-width: ${BREAKPOINT_MD}) {
                    font-size: 24px;
                  height: 24px;
                }
              order: 1;
              align-self: center;
              padding: 0px 5px;
              height: 24px;
              font-size: 24px;
              color: #73859d;
              cursor: pointer;
              &.bx-x {
                font-size: 22px;
                padding: 5px;
                 background:#e8ecf7;;
                height: 22px;
                line-height: 22px ;
                color: #73859d;
                border-radius: 50%;
                cursor: pointer;
               &:hover{
                 background:#cbd5e0;
               }
              }
        }}
      z-index: 23;
      margin-bottom: 25px;
      position: relative;
      display: block;
      padding: 5px 15px;
      margin: 0 auto;
      max-width: 600px;  
      background:#e8ecf7;
    border-radius: ${BUTTON_RADIUS};
    
    }
`;
class SearchForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            search: "",
            isFocused: false,
            resultVisible: false,
            lastScrollPos: 0,
            searchClass:'search-cont',
            style:'ALL',
            category:{id:null,name:null}
        };

        this.onChange = this.onChange.bind(this);
        this.emptySearch = this.emptySearch.bind(this);
this.onSearchFocus = this.onSearchFocus.bind(this);
this.onSearchBlur = this.onSearchBlur.bind(this);
        this.handleSearchScroll = this.handleSearchScroll.bind(this);
    }

    emptySearch() {
      this.setState({searchClass:'search-cont'});
        this.setState({
            search: ""
        });
        setTimeout(function() {
          if(this.state.category!=null){
            this.props.setSearchTerm("",this.state.style,this.state.category.id);}
            else{
              this.props.setSearchTerm("",this.state.style,null);
            }
        }.bind(this),
            100
        );
    }
    onChange(e) {
        this.setState({
            search: e.target.value
        });
       
        
        setTimeout(function() {
            if(this.state.category!=null){
              this.props.setSearchTerm(this.state.search,this.state.style,this.state.category.id);
            }
              else{
                this.props.setSearchTerm(this.state.search,this.state.style,null);
           
              }
            
        }.bind(this),
            100
        );
    }
    onSearchFocus(){
        this.setState({searchClass:'search-cont active'});
    }
     onSearchBlur(){
       
        this.setState({searchClass:'search-cont'});
    }
    handleSearchScroll() {
        var header = document.querySelector(".navbar-cont  .search-sticky");
        var navbar = document.querySelector(".navbar-cont");
        var sticky = document.querySelector(".home-top .search-sticky");
        var focusEl = document.querySelector(".navbar-cont  .search-box input");
        var searchEl = document.querySelector(".home-top .search-box input");
        var isFocused = (document.activeElement === focusEl);  
        var isMainFocused = (document.activeElement === searchEl);    
        var fixed_class = "nav-active";
        if(sticky!==null){
        var rect = sticky.getBoundingClientRect();

        if (rect.top < 0) {
        navbar.classList.add(fixed_class);
            header.classList.add(fixed_class);
             if(isMainFocused){focusEl.focus();};
        } else {
             if(isFocused){searchEl.focus();};
            header.classList.remove(fixed_class);
            navbar.classList.remove(fixed_class);
        }}
    }
    
    componentWillReceiveProps(newProps){
        this.setState({search:newProps.search,category:newProps.category});
        this.setState({style:newProps.style});

    }

    componentDidMount() {
        window.addEventListener("scroll", this.handleSearchScroll);
    }
    componentWillUnmount() {
        window.removeEventListener("scroll", this.handleSearchScroll);
    }
    render() {
        return (
            <StyledSearch className="search-sticky animated fadeIn">
        <div className={this.state.searchClass} onFocus={this.onSearchFocus} onBlur={this.onSearchBlur}>
         
            <div className="search-box" ref="searchBox">
            <i className="bx bx-search"></i>
              <input
            onChange={this.onChange}
            value={this.state.search}
            autoComplete="off"
            placeholder="Search icons..."
            />
              {this.state.search.length > 0 &&
                <i className="bx bx-x" onClick={this.emptySearch}></i>
                 }
            </div>
         
        </div>
      </StyledSearch>
        );
    }
}

function mapStateToProps(state) {
  return {
    search: state.search,
    category: state.category,
    style: state.style
  };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators(
        {
            fetchIcons,
            setSearchTerm,
            changeSideIcon, 
            setSelectedIcon,
        },
        dispatch
    );
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SearchForm);